import Vue from 'vue'
import Vuex from 'vuex'
// import createMutationsSharer from 'vuex-shared-mutations'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    collection: 'All',
    userDB: {collections: [{knowledges: ''}]},
    isLogged: false,
    addMode: false,
    editMode: false,
    typeofEdit: '',
    allwords: [],
    learningMode: false,
    cards: [],
    testMode: false,
    language: {},
    en: {home:'Home',collections:'Collections',withoutColl:'Without collection',manage:'Manage',learning:'Learning',check:'Check',logout:'Logout',homeTitle:'Learn anything you want',homeTitleLead:'It\'s easy with associative system!',wordsTitleLogin:'Add something you need to learn and its meaning:',wordsInput1:'Word',wordsInput2:'Meaning',btnConfirm:'Confirm',btnGo:'Go to collections',logoutStart:'Let\'s start!',logoutSignUp: "You should sign up to save your knowledges, don't worry, because it's easy and fast!",logoutGoogle:'Sign in with Google',manageTitle:'Manage collections',btnAddColl:'Add collection',all:'All',btnAddKnows:'Add knowledges',btnRemoveKnows:'Remove knowledges',btnSelAdd:'Add selected knowledges',btnSelRemove:'Remove selected knowledges',emptyColl:"There's nothing here yet",addCollName:'Name',btnAdd:'Add',learningStart:'Select the collection you want to learn and press start:',btnGotIt:'Got it ',btnStart:'Start',popupHead1:'You just finished',popupHead2:'collection',popupBody:"Now you can check yourself, click 'Check' ",btnOk:'Okay',checkTitle:'Test your knowledge', checkEnd1:'Congratulations! Your result is ', checkEnd2: ' from ',btnNext:'Next'},
    ru: {home:'На главную',collections:'Коллекции',withoutColl:'Без коллекции',manage:'Управление',learning:'Обучение',check:'Проверка',logout:'Выход',homeTitle:'Выучи всё, что хочешь',homeTitleLead:'Это просто с ассоциативной системой!',wordsTitleLogin:'Напиши что-нибудь и добавь его значение',wordsInput1:'Слово',wordsInput2:'Значение',btnConfirm:'Подтвердить',btnGo:'Коллекции',logoutStart:'Начнём!',logoutSignUp: "Тебе необходимо зарегистрироваться. Не беспокойся, это легко и быстро!",logoutGoogle:'Войти через Google+',manageTitle:'Управление коллекциями',btnAddColl:'Добавить коллекцию',all:'Все',btnAddKnows:'Добавить',btnRemoveKnows:'Удалить',btnSelAdd:'Добавить выбранное',btnSelRemove:'Удалить выбранное',emptyColl:"Здесь пока-что пусто.",addCollName:'Название',btnAdd:'Добавить',learningStart:'Выбери коллекцию, которую ты хочешь выучить и нажми начать:',btnGotIt:'Понял ',btnStart:'Начать',popupHead1:'Ты закончил',popupHead2:'коллекцию',popupBody:"Теперь ты можешь проверить себя, нажми 'Проверка' ",btnOk:'Окей',checkTitle:'Проверь свои знания', checkEnd1:'Поздравляю! Твой результат ', checkEnd2: ' из ',btnNext:'Далее'}
  },
  mutations: {
    setState (state, [name, data]) {
      state[name] = data
    },
    toggle (state, [name]) {
      state[name]
        ? state[name] = false
        : state[name] = true
    }
  },
  getters: {
    collection (state) {
      return state.collection
    },
    userDB (state) {
      return state.userDB
    },
    logstatus (state) {
      return state.isLogged
    },
    addMode (state) {
      return state.addMode
    },
    editMode (state) {
      return state.editMode
    },
    typeofEdit (state) {
      return state.typeofEdit
    },
    allwords (state) {
      return state.allwords
    },
    cards (state) {
      return state.cards
    },
    learningMode (state) {
      return state.learningMode
    },
    testMode (state) {
      return state.testMode
    },
    language (state) {
      return state.language
    },
    en (state) {
      return state.en
    },
    ru (state) {
      return state.ru
    }

  }
  // plugins: [createMutationsSharer({ predicate: ['setState', 'toggle'] })]
})



